<template>
  <div>
    <div v-if="items.length">
      <table class="table table-bordered table-condensed">
        <colgroup>
          <col :style="{ width: '5%' }" />
          <col :style="{ width: '50%' }" />
          <col :style="{ width: '10%' }" />
          <col :style="{ width: '25%' }" />
        </colgroup>
        <tr>
          <th>번호</th>
          <th>제목</th>
          <th>작성자</th>
          <th>날짜</th>
        </tr>
        <list-row
          v-for="(item, index) in items"
          :key="`${index}_items`"
          :boardId="item.boardId"
          :title="item.title"
          :userSeq="item.userSeq"
          :regDt="item.regDt"
        />
      </table>
    </div>
    <div v-else>글이 없습니다.</div>
    <div class="text-right" v-if="uid=='admin'">
      <button class="btn btn-primary" @click="movePage">등록</button>
    </div>
  </div>
</template>

<script>
import http from '@/util/http-common';
import ListRow from '@/components/Row.vue';
import store from "@/store/store.js";

export default {
  name: 'list',
  components: {
    ListRow,
  },
  data: function() {
    return {
      items: [],
      isLogin: false,
      uid: '',
    };
  },
  mounted() {
    this.isLogin = store.state.isLogin;
    this.uid = store.state.uid;
    console.log(this.uid);
    http
      .get('/board')
      .then(({ data }) => {
        console.log(data);
        this.items = data;
      })
      .catch(() => {
        alert('에러가 발생했습니다.');
      });
  },
  methods: {
    movePage() {
      this.$router.push('/board/create');
    },
  },
};
</script>

